<template>
    <div>
        <el-table
                :data="measuredList"
                :fit="true"
                style="max-height: 100%">

            <el-table-column
                    label="日期"
                    prop="date"
                    align="center"
                    sortable
                    :formatter="getDateStringFromTimestamp"
            ></el-table-column>
            <el-table-column
                    label="水库"
                    prop="siteName"
                    align="center"
                    :filters="filters.reservoir"
                    :filter-method="filterHandler"
            ></el-table-column>
            <el-table-column
                    label="水位/m"
                    prop="waterLevel"
                    header-align="center"
                    align="center"
                    sortable
            ></el-table-column>
            <el-table-column
                    label="蓄水量/亿m³"
                    prop="measuredStorage"
                    header-align="center"
                    align="center"
                    :formatter="getMeasuredStorage"
                    sortable
            ></el-table-column>
            <el-table-column
                    fixed="right"
                    label="操作"
                    width="100"
                    align="center">
                <template slot-scope="scope">
                    <el-popconfirm
                            title="操作不可复原，确定删除吗？"
                            icon="el-icon-info"
                            iconColor="red"
                            @onConfirm="handleSingleDeleteClick(scope.row.id)"
                    >
                        <el-button slot="reference"
                                   type="text"
                                   size="small">
                            删除
                        </el-button>
                    </el-popconfirm>

                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        name: "dataManagementMeasuredTable",
        data: function () {
            return {
                measuredList: [],
                filters:{
                    reservoir:[],
                }
            }

        },
        methods: {
            getDateStringFromTimestamp: function(row){
                return new Date(row.date).toLocaleDateString();
            },
            getMeasuredStorage: function(row){
                if (row.measuredStorage*1 === 0) return '-';
                return row.measuredStorage
            },
            filterHandler(value, row, column) {
                const property = column['property'];
                return row[property] === value+'';
            },
            handleSingleDeleteClick: function (id) {
                this.axios.get('/api/data/measured/delete',{
                    params:{
                        sList: id,
                    }
                }).then((response)=>{
                    if (response.status===200){
                        this.measuredList.splice(this.measuredList.findIndex((x)=>{
                            return x.id === id;
                        }), 1);
                        this.$notify({
                            title: '成功',
                            message: '记录已删除',
                            type: 'success'
                        });
                    }
                })
            },
            loadData: function(){
                const self = this;
                this.axios.get('/api/data/measured').then((response) => {
                    if (response.status === 200) {
                        this.measuredList = response.data;
                        for (let i = 0; i<this.measuredList.length;i++){
                            if (this.filters.reservoir.find((x)=>{
                                return x.text===this.measuredList[i].siteName
                            }) === undefined){
                                self.filters.reservoir.push({
                                    text: this.measuredList[i].siteName,
                                    value: this.measuredList[i].siteName,
                                })
                            }
                        }
                    } else {
                        console.log('error');
                    }
                })
            }
        },
        mounted() {
            this.loadData();
        }
    }
</script>

