<template>
    <el-container>
        <el-header>
            数据管理
        </el-header>
        <el-main>
            <el-card shadow="hover">
                <el-tabs v-model="tabsActive">
                    <el-tab-pane label="实测数据" name="measured">
                        <measured-table/>
                    </el-tab-pane>
                    <el-tab-pane label="遥测水位" name="radar">
                        <radar-table/>
                    </el-tab-pane>
                    <el-tab-pane label="水域面积" name="area">
                        <area-table/>
                    </el-tab-pane>
                    <el-tab-pane label="影像管理" name="image">
                        <image-table v-if="showImageTab"/>
                    </el-tab-pane>
                    <el-tab-pane label="模型管理" name="model">
                        <el-row :gutter="10" style="height: 100px">
                            <el-col :span="6">
                                <el-button
                                        type="primary"
                                        icon="el-icon-set-up"
                                        @click="handleEditLevelModelsClick">
                                    水位模型
                                </el-button>
                            </el-col>
                            <el-col :span="6">
                                <el-button
                                        type="success"
                                        icon="el-icon-set-up"
                                        @click="handleEditAreaModelsClick">水域面积模型
                                </el-button>
                            </el-col>
                            <el-col :span="6">
                                <el-button
                                        type="warning"
                                        icon="el-icon-set-up"
                                        @click="handleEditStorageModelsClick">蓄水量模型
                                </el-button>
                            </el-col>
                        </el-row>
                    </el-tab-pane>
                    <el-tab-pane label="水库管理" name="reservoir">
                        <el-row :gutter="10" style="height: 100px">
                            <el-col :span="6">
                                <el-button
                                        type="primary"
                                        icon="el-icon-add-location"
                                        @click="handleAddReservoirClick">
                                    添加水库
                                </el-button>
                            </el-col>
                            <el-col :span="6">
                                <el-button type="info" icon="el-icon-delete-location"
                                @click="handleEditReservoirClick">修改/删除水库</el-button>
                            </el-col>
                        </el-row>

                    </el-tab-pane>
                </el-tabs>
            </el-card>

        </el-main>
    </el-container>
</template>

<script>
    import measuredTable from "@/components/dataManagementMeasuredTable";
    import radarTable from "@/components/dataManagementRadarTable";
    import areaTable from "@/components/dataManagementAreaTable";
    import imageTable from "@/components/dataManagementImageTable";

    export default {
        name: "dataManagement",
        components: {
            imageTable,
            measuredTable,
            radarTable,
            areaTable,
        },
        props: [],
        data: function () {
            return {
                tabsActive: 'measured',
                showedImageTab: false,
            }
        },
        computed:{
            showImageTab: function () {
                const self = this;
                if (self.tabsActive === 'image'){
                    self.showedImageTab = true;
                }
                return self.showedImageTab;
            }
        },
        methods: {
            handleEditLevelModelsClick: function () {
                this.$router.push('/model/level');
            },
            handleEditAreaModelsClick: function () {
                this.$router.push('/model/area');
            },
            handleEditStorageModelsClick: function () {
                this.$router.push('/model/storage');
            },
            handleAddReservoirClick: function () {
                this.$router.push('/add-reservoir');
            },
            handleEditReservoirClick: function () {
                this.$router.push('/edit-reservoir');
            },
        }
    }
</script>

<style scoped>
    .el-button {
        height: 100px;
        width: 100%;
        font-size: 20px;
    }

    .el-card {
        border: 1px solid #d4d4d4;
        width: 100%;
    }
</style>
