<template>
    <div>
        <el-table
                :data="imageList"
                :fit="true">

            <el-table-column
                    label="水库"
                    prop="rname"
                    width="100"
                    align="center"
                    :filters="filters.reservoir"
                    :filter-method="filterHandler"
            ></el-table-column>
            <el-table-column
                    label="缩略图"
                    width="100"
                    align="center"
            >
                <template slot-scope="scope">
                    <el-image
                            :src="'/api/data/getImageData?type='+scope.row.type+'&id='+scope.row.id"
                            :preview-src-list="['/api/data/getImageData?type='+scope.row.type+'&id='+scope.row.id]"></el-image>
                </template>
            </el-table-column>
            <el-table-column
                    label="图片路径"
                    prop="path"
                    header-align="center"
            ></el-table-column>
            <el-table-column
                    label="日期"
                    prop="date"
                    width="150"
                    align="center"
                    sortable
            ></el-table-column>
            <el-table-column
                    label="数据来源"
                    prop="satelliteName"
                    width="100"
                    align="center"
                    :filters="filters.satelliteName"
                    :filter-method="filterHandler"
            ></el-table-column>
            <el-table-column
                    fixed="right"
                    label="操作"
                    width="150"
                    align="center">
                <template slot-scope="scope">
                    <el-tooltip effect="dark" content="下载" placement="left">
                        <el-button
                                @click="download(scope.row.type,scope.row.id)"
                                size="small" icon="el-icon-download" circle>
                        </el-button>
                    </el-tooltip>
                    <el-tooltip effect="dark" content="删除" placement="right">
                        <el-popconfirm
                                title="操作不可复原，确定删除吗？"
                                icon="el-icon-info"
                                iconColor="red"
                                @onConfirm="handleSingleDeleteClick(scope.row.id, scope.row.type)"
                        >
                            <el-button slot="reference" size="small" icon="el-icon-delete" circle>
                            </el-button>
                        </el-popconfirm>
                    </el-tooltip>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        name: "dataManagementImageTable",
        data: function () {
            return {
                imageList: [],
                filters: {
                    reservoir: [],
                    satelliteName: [],
                }
            }

        },
        methods: {
            download: function (type, id) {
                let elemIF = document.createElement('iframe');
                elemIF.src = '/api/download/image?type=' + type + '&id=' + id;
                elemIF.style.display = 'none';
                document.body.appendChild(elemIF)
            },
            filterHandler(value, row, column) {
                const property = column['property'];
                return row[property] === value + '';
            },
            handleSingleDeleteClick: function (id, type) {
                this.axios.get('api/data/image/delete', {
                    params: {
                        id: id,
                        type: type
                    }
                }).then((response) => {
                    if (response.status === 200) {
                        this.imageList.splice(this.imageList.findIndex((x) => {
                            return x.id === id;
                        }), 1);
                        this.$notify({
                            title: '成功',
                            message: '影像已删除',
                            type: 'success'
                        });

                    }
                })
            }
        },
        mounted() {
            const self = this;
            this.axios.get('/api/data/image').then((response) => {
                if (response.status === 200) {
                    this.imageList = response.data;
                    for (let i = 0; i < this.imageList.length; i++) {
                        if (this.filters.reservoir.find((x) => {
                            return x.text === this.imageList[i].rname
                        }) === undefined) {
                            self.filters.reservoir.push({
                                text: this.imageList[i].rname,
                                value: this.imageList[i].rname,
                            })
                        }
                        if (this.filters.satelliteName.find((x) => {
                            return x.text === this.imageList[i].satelliteName
                        }) === undefined) {
                            self.filters.satelliteName.push({
                                text: this.imageList[i].satelliteName,
                                value: this.imageList[i].satelliteName,
                            })
                        }
                    }
                } else {
                    console.log('error');
                }
            })
        }
    }
</script>

