<template>
    <div>
        <el-table
                :data="areaList"
                :fit="true"
                lazy>

            <el-table-column
                    label="日期"
                    prop="date"
                    align="center"
                    sortable
                    :formatter="getDateStringFromTimestamp"
            ></el-table-column>
            <el-table-column
                    label="水库"
                    prop="reservoirName"
                    align="center"
                    :filters="filters.reservoir"
                    :filter-method="filterHandler"
            ></el-table-column>
            <el-table-column
                    label="水域面积/km²"
                    prop="area"
                    align="center"
                    sortable
            ></el-table-column>
            <el-table-column
                    label="来源"
                    prop="type"
                    header-align="center"
                    align="center"
                    :filters="filters.type"
                    :filter-method="filterHandler"
            ></el-table-column>
            <el-table-column
                    fixed="right"
                    label="操作"
                    width="100"
                    align="center">
                <template slot-scope="scope">
                    <el-popconfirm
                            title="操作不可复原，确定删除吗？"
                            icon="el-icon-info"
                            iconColor="red"
                            @onConfirm="handleSingleDeleteClick(scope.row.id)"
                    >
                        <el-button slot="reference"
                                   type="text"
                                   size="small">
                            删除
                        </el-button>
                    </el-popconfirm>

                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        name: "dataManagementAreaTable",
        data: function () {
            return {
                areaList: [],
                filters:{
                    reservoir:[],
                    type:[],
                }
            }

        },
        methods: {
            getDateStringFromTimestamp: function(row){
                return new Date(row.date).toLocaleDateString();
            },
            filterHandler(value, row, column) {
                const property = column['property'];
                return row[property] === value+'';
            },
            handleSingleDeleteClick: function (id) {
                this.axios.get('/api/data/area/delete',{
                    params:{
                        sList: id,
                    }
                }).then((response)=>{
                    if (response.status===200){
                        this.areaList.splice(this.areaList.findIndex((x)=>{
                            return x.id === id;
                        }), 1);
                        this.$notify({
                            title: '成功',
                            message: '记录已删除',
                            type: 'success'
                        });
                    }
                })
            },
            loadData: function () {
                const self = this;
                this.axios.get('/api/data/area').then((response) => {
                    if (response.status === 200) {
                        this.areaList = response.data;
                        for (let i = 0; i<this.areaList.length;i++){
                            if (this.filters.reservoir.find((x)=>{
                                return x.text===this.areaList[i].reservoirName
                            }) === undefined){
                                self.filters.reservoir.push({
                                    text: this.areaList[i].reservoirName,
                                    value: this.areaList[i].reservoirName,
                                })
                            }
                            if (this.filters.type.find((x)=>{
                                return x.text===this.areaList[i].type
                            }) === undefined){
                                self.filters.type.push({
                                    text: this.areaList[i].type,
                                    value: this.areaList[i].type,
                                })
                            }
                            this.areaList[i].area = this.areaList[i].area * 0.000001
                        }
                    } else {
                        console.log('error');
                    }
                })
            }
        },
        mounted() {
            this.loadData();
        }
    }
</script>

